import React from "react";
import "./sunmissions.scss"
import {Link} from "react-router-dom"
import Header  from "../../Components/Header/Header"
import Pages_menu from "../../Components/Pages-menu/Pages_menu"
import Footer from "../../Components/Footer/Footer"
import unnamed from "../../asseste/images/unnamed.png"
function Sunmissions () {
    return (
        <>
        <Header />
            <div className="sub_title">
                {
                    document.querySelector("title").textContent = "Submissions | Archive of Conferences"
                }
            </div>
            <div className="pages_sunmissions container">
                <div className="pages_sunmissions_body">
                    <div className="ushoq">
                        <h6><Link className="link" to="/">HOME </Link> / <Link className="link" to="/submissions"> SUBMISSIONS</Link ></h6>
                    </div>
                    <h1>Submissions</h1>
                    <div className="alert alert-primary "> 
                    Login or Register to make a submission.
                    </div>
                    <h2>
                    Submission Preparation Checklist
                    </h2>
                    <p className="description">As part of the submission process, authors are required to check off their submission's compliance with all of the following items, and submissions may be returned to authors that do not adhere to these guidelines.</p>

                    <ul className="list-group">
                        <li className="list-group-item">
                            <img width="20px" height="20px" src={unnamed} alt="" />
                            The submission has not been previously published, nor is it before another journal for consideration (or an explanation has been provided in Comments to the Editor).
                        </li>
                        <li className="list-group-item">
                            <img width="20px" height="20px" src={unnamed} alt="" />
                            The submission file is in OpenOffice, Microsoft Word, or RTF document file format.
                        </li>
                        <li className="list-group-item">
                            <img width="20px" height="20px" src={unnamed} alt="" />
                            Where available, URLs for the references have been provided.
                        </li>
                        <li className="list-group-item">
                            <img width="20px" height="20px" src={unnamed} alt="" />
                            The text is single-spaced; uses a 12-point font; employs italics, rather than underlining (except with URL addresses); and all illustrations, figures, and tables are placed within the text at the appropriate points, rather than at the end.
                        </li>
                        <li className="list-group-item">
                            <img width="20px" height="20px" src={unnamed} alt="" />
                            The text adheres to the stylistic and bibliographic requirements outlined in the Author Guidelines.
                        </li>
                    </ul>
                </div>
                <div >
                    <Pages_menu />
                </div>
            </div>
            <Footer />
        </>
    )
}

export default Sunmissions;