import React from "react"
import "./Archives.scss"
import Header  from "../../Components/Header/Header"
import Pages_menu from "../../Components/Pages-menu/Pages_menu"
import Footer from "../../Components/Footer/Footer"
import {Link, useParams} from "react-router-dom"
import {useEffect, useRef, useState} from "react"
function Archives_more () {
    const {archive_id} = useParams()
    const [data_articles, setdata_articles] = useState([])
    const [data_archive, setdata_archive] = useState({
        "archives_id": "3",
        "archives_date": "2022-01-13",
        "archives_img": "http://uploadimg.systemfront.uz/images/myImage-System-front-1644044217734.jpg",
        "archives_title": "HSC"
    })
    useEffect(() => {
        fetch(`https://conferencepublication-backend.herokuapp.com/one_archive?Archives_id=${archive_id}`, {
            method: "GET"
        }).then(res => res.json()).then(res => {
            setdata_archive(res)
        })
    }, []);
    useEffect(() => {
        fetch(`https://conferencepublication-backend.herokuapp.com/articles_for_archive?articles_archive=${archive_id}`, {
            method: "GET"
        }).then(res => res.json()).then(res => {
            res.sort((a, b) => a.articles_page - b.articles_page)
            setdata_articles(res)
        })
    }, []);
    return (
        <>
            <Header />
            <div className="sub_title">
                {
                    document.querySelector("title").textContent = `${data_archive.archives_date.split("-")[0]}: ${data_archive.archives_title} | Archive of Conferences
                        `
                }
            </div>
            <div className="pages_archives_more container">
                <div className="pages_archives_more_body">
                    <div className="ushoq">
                        <h6><Link className="link" to="/">HOME </Link> / <Link className="link" to="/archives"> ARCHIVES</Link > / <Link className="link" to="/">{data_archive.archives_date.split("-")[0]}: {data_archive.archives_title}</Link ></h6>
                    </div>     
                    <div className="archive-more">
                        <img src={data_archive.archives_img} alt="" />
                        <p><span>Published:</span>{data_archive.archives_date}</p>
                    </div>
                    <h3>Articles</h3>
                    <div className="archive-article">
                         {data_articles.map((elem) => {
                             return (
                                 <div className="archive-articles_item">
                                     <Link className="archive-articles_item_link" to={`/articles-more/${elem.articles_id}`}>{elem.articles_title}</Link>
                                     <h6>{elem.articles_author}</h6>
                                     <p>{elem.articles_page}</p>
                                     <Link className="link_pdf btn btn-primary" to={`/open_pdf/${elem.articles_id}`}>PDF</Link>
                                 </div>
                             )
                         })}
                    </div>
                </div>
                <div >
                    <Pages_menu />
                </div>
            </div>
            <Footer />
        </>
    )
}

export default Archives_more;