import React from "react";
import "./about_publishing_system.scss"
import Header  from "../../Components/Header/Header"
import Pages_menu from "../../Components/Pages-menu/Pages_menu"
import Footer from "../../Components/Footer/Footer"
import {Link} from "react-router-dom"
import pubprocesslarge from "../../asseste/images/pubprocesslarge.png"
function About_publishing_system () {
    return (
        <>
        <Header />
        <div className="sub_title">
                {
                    document.querySelector("title").textContent = "About this Publishing System | Archive of Conferences"
                }
            </div>
            <div className="pages_about_publishing_system container">
                <div className="pages_about_publishing_system_body">
                    <div className="ushoq">
                        <h6><Link className="link" to="/">HOME </Link> / <Link className="link" to="/aboutPublishingSystem"> ABOUT THIS PUBLISHING SYSTEM </Link ></h6>
                    </div>
                    <h1>About this Publishing System</h1>
                    <p>
					    This journal uses Open Journal Systems 3.2.0.3, which is open source journal management and publishing software developed, supported, and freely distributed by the <a href="http://pkp.sfu.ca/">Public Knowledge Project</a> under the GNU General Public License.
			        </p>
                    <img src={pubprocesslarge} alt="" />
                </div>
                <div >
                    <Pages_menu />
                </div>
            </div>
            <Footer />
        </>
    )
}
export default About_publishing_system;