import React from "react";
import "./open_pdf.scss"
import {useParams} from "react-router-dom"
import {useState, useEffect} from "react"
		
function Open_pdf () {
    const {id} = useParams()
    const [data_iframe, set_data_iframe] = useState([])
    useEffect(() => {
        fetch(`https://conferencepublication-backend.herokuapp.com/one_articles?articles_id=${id}`, {
            method: "Get"
        }).then(res => res.json()).then(res => {
            set_data_iframe(res)
        })  
    }, []);
    return (
        <>
           <div className="sub_title">
                {
                    document.querySelector("title").textContent = `View of ${data_iframe.articles_title}`
                }
            </div>
            <iframe className="iframe_pdf" src={data_iframe.articles_pdf} width="100%" height="100vh">
            </iframe>
        </>
    )
}


export default Open_pdf;