import React from "react"
import "./Archives.scss"
import {useState, useEffect} from "react"
import Header  from "../../Components/Header/Header"
import Pages_menu from "../../Components/Pages-menu/Pages_menu"
import Footer from "../../Components/Footer/Footer"
import {Link} from "react-router-dom"
function Archives_Page () {
    const [data_archives_user, set_data_user] = useState([])
    useEffect(() => {
        fetch("https://conferencepublication-backend.herokuapp.com/archives", {
            method: "Get"
        }).then(res => res.json()).then(res => {
            res.sort((a, b) => a.archives_id - b.archives_id)
            set_data_user(res)
        })
    }, []);
    return (
        <>
            <Header />
            <div className="sub_title">
                {
                    document.querySelector("title").textContent = "Archives | Archive of Conferences"
                }
            </div>
            <div className="pages_archives_Page container">
                <div className="pages_archives_Page_body">
                    <div className="ushoq">
                        <h6><Link className="link" to="/">HOME </Link> / <Link className="link" to="/"> ARCHIVES</Link ></h6>
                    </div>     
                    <div className="archives-list-user">
                        
                    {
                        data_archives_user.slice(0).reverse().map((elem) => {
                            let data = elem.archives_date.split("-")
                            return (
                            <Link to={`/archives-more/${elem.archives_id}`} className="archies-list-user-itme">
                                <img src={elem.archives_img} alt="" />
                                <div>
                                    <h5>{elem.archives_title}</h5>
                                    <p>{data[0]}</p>
                                </div>
                            </Link>)
                        })
                    }
                    </div>
                </div>
                <div >
                    <Pages_menu />
                </div>
            </div>
            <Footer />
        </>
    )
}
export default Archives_Page;