import React from "react";
import "./Footer.scss"
import footer_logo from "../../asseste/images/ojs_brand.png"
import {Link} from "react-router-dom"
function Footer () {
    return (
        <footer>
            <div className="container">
            <Link to="/aboutPublishingSystem"><img src={footer_logo} alt="" /></Link>
            
            </div>
        </footer>
    )
}

export default Footer;