import React from "react";
import Header_admin from "../../Components/Header-admin/Header-admin";
import "./Home-admin.scss"
import {useEffect, useRef, useState} from "react"
function Papers_page() {
    const paper_title  = useRef()
    const [photo_paper, setphotopaper] = useState()
    const [data_paper, set_data_paper] = useState([])
    useEffect(() => {
       fetch("https://conferencepublication-backend.herokuapp.com/papers", {
           method: "GET"
       }).then(res => res.json()).then(res => {
          set_data_paper(res)
       })
    }, []);
    return (
        <>
            <Header_admin />
            <h1 className="text-center m-2">New Paper</h1>
            <form className="container new-arcives-list">
                <input ref={paper_title} type="text" placeholder="Archive title" />
                <input type="file" className="input-photo6 form-control"   id="inputGroupFile04" aria-describedby="inputGroupFileAddon04" aria-label="Upload" onChange={(e) => {
                     var formdata = new FormData();
                     formdata.append("myImage", document.querySelector(".input-photo6").files[0]);
                     
                     var requestOptions = {
                       method: 'POST',
                       body: formdata,
                       redirect: 'follow'
                     };
                     
                     fetch("http://uploadimg.systemfront.uz/upload-image", requestOptions)
                       .then(response => response.text())
                       .then(result => {
                        setphotopaper(result)
                       })
                       .catch(error => console.log('error', error));
                }} />
                <button className="btn" onClick={(e) => {
                    e.preventDefault()
                    var myHeaders = new Headers();
                    myHeaders.append("Content-Type", "application/json");

                    var raw = JSON.stringify({
                        "paper_img": photo_paper,
                        "paper_title": paper_title.current.value,
                        "token": window.localStorage.getItem("token")
                    });

                    var requestOptions = {
                    method: 'POST',
                    headers: myHeaders,
                    body: raw,
                    redirect: 'follow'
                    };

                    fetch("https://conferencepublication-backend.herokuapp.com/new_paper", requestOptions)
                    .then(response => response.text())
                    .then(result => {
                        console.log(result)
                        if(result == "ok") {
                            alert("succses")
                            window.location.reload()
                        }else{
                            alert("error: qandaydur hatolik bor iltmos keyinroq urnib koring")
                        }
                    })
                    .catch(error => console.log('error', error));
                }}>Send</button>
            </form>
            <div className="container articles-list">
                {data_paper.map((elem) => {
                    return (
                         <div key={elem.paper_id} class="card" >
                                <img src={elem.paper_img} width="250px" class="card-img-top"/>
                                <div class="card-body">
                                <h5 class="card-title">{elem.paper_title}</h5>
                                <button className="btn" data-uuid={elem.paper_id} onClick={(e) => {
                                       let sorov = prompt("rostan ham bu malumotni ochirmoqchimisiz (Y/n)")
                                       if(sorov === "Y"){
                                        var myHeaders = new Headers();
                                        myHeaders.append("Content-Type", "application/json");
                                        var raw = JSON.stringify({
                                            "paper_id": e.target.dataset.uuid,
                                            "token": window.localStorage.getItem("token")
                                        })
                                        var requestOptions = {
                                            method: 'POST',
                                            headers: myHeaders,
                                            body: raw,
                                            redirect: 'follow'
                                            };
                                        fetch("https://conferencepublication-backend.herokuapp.com/delete_paper", requestOptions).then(res => res.text()).then(res => {
                                            console.log(res)
                                            if(res == "ok") {
                                                window.location.reload()
                                            }
                                        })
                                       }else {

                                       }
                                    }}>Delete</button>
                                    </div>
                                </div>
                    )
                })}
            </div>
        </>
    )
}

export default Papers_page;