import React from "react";
import "./about_contact.scss"
import { Link } from "react-router-dom";
import Header  from "../../Components/Header/Header"
import Pages_menu from "../../Components/Pages-menu/Pages_menu"
import Footer from "../../Components/Footer/Footer"

function About_contact () {
    
    return (
        <>
     
          <Header />
          <div className="sub_title">
                {
                    document.querySelector("title").textContent = "Contact | Archive of Conferences"
                }
            </div>

            <div className="pages_about_contact container">
                <div className="pages_about_contact_body">
                    <div className="ushoq">
                        <h6><Link className="link" to="/">HOME </Link> / <Link className="link" to="/submissions">CONTACT</Link ></h6>
                    </div>
                    <div>
                        <div className="contact_1">
                            <h1>Contact</h1>
                            <span>1C/14 Ramesh Nagar</span>
                            <span>Delhi Pin Code 110015</span>
                        </div>
                        <div className="contact_2">
                            <h2>Principal Contact</h2>
                            <span>Dr. Anjali Sharma</span>
                            <span>Editor, Archive of Conferences</span>
                            <a type="email" href="mailto:editor@conferencepublication.com">editor@conferencepublication.com</a>
                        </div>
                        <div className="contact_3">
                            <h2>Support Contact</h2>
                            <span>Mr. Shaurya Sharma</span>
                            <a type="email"href="mailto:support@conferencepublication.com">support@conferencepublication.com</a>
                        </div>
                    </div>
                </div>
                <div >
                    <Pages_menu />
                </div>
            </div>
            <Footer />
        </>
    )
}

export default About_contact;