import React from "react";
import "./Papers.scss"
import Header  from "../../Components/Header/Header"
import Pages_menu from "../../Components/Pages-menu/Pages_menu"
import Footer from "../../Components/Footer/Footer"
import {useState, useEffect} from "react"
function Papers_page_user () {
    const [data_paper_user, set_data_paper_user] = useState([
        {
            "paper_id": "2",
            "paper_title": "Call for Papers for upcoming conferences in November-2021.",
            "paper_img": "http://uploadimg.systemfront.uz/images/myImage-System-front-1644064185696.png"
        }
    ])
    useEffect(() => {
        fetch("https://conferencepublication-backend.herokuapp.com/papers", {
            method: "GET"
        }).then(res => res.json()).then(res => {
           
            set_data_paper_user(res)
        })
     }, []);
    return (
        <>
          <Header />
            <div className="sub_title">
                {
                    document.querySelector("title").textContent = `Call for Papers | Archive of Conferences`
                }
            </div>
            <div className="pages_papers_page container">
                <div className="pages_papers_page_body">
                    <h2>Call for Papers</h2>
                    <div>
                        <p>{data_paper_user[0].paper_title}</p>
                        <img src={data_paper_user[0].paper_img} width="730px"
                     alt="" />
                    </div>
                </div>
                <div >
                    <Pages_menu />
                </div>
            </div>
            <Footer />
        </>
    )
}

export default Papers_page_user;