import React from "react";
import "./information.scss"
import { useParams, Link } from "react-router-dom";
import Header  from "../../Components/Header/Header"
import Pages_menu from "../../Components/Pages-menu/Pages_menu"
import Footer from "../../Components/Footer/Footer"
import {useEffect, useState, useRef} from "react"



function Information () {
    const [data, setdata] = useState({
        "readers": {
            "ushoq_text": " INFORMATION FOR READERS",
            "title": "Information For Readers",
            "body": `<div class="description">
                We encourage readers to sign up for the publishing notification service for this journal. Use the <a href="">Register</a> link at the top of the home page for the journal. This registration will result in the reader receiving the Table of Contents by email for each new issue of the journal. This list also allows the journal to claim a certain level of support or readership. See the journal's <a href="">Privacy Statement</a>, which assures readers that their name and email address will not be used for other purposes.
            </div>`
        },
        "authors": {
            "ushoq_text": " INFORMATION FOR AUTHORS",
            "title": "Information For Authors",
            "body": `
            <div class="description">
                Interested in submitting to this journal? We recommend that you review the <a href="http://ojs.conferencepublication.com/index.phpaoc/about">About the Journal</a> page for the journal's section policies, as well as the <a href="http://ojs.conferencepublication.com/index.phpaoc/about/submissions#authorGuidelines">Author Guidelines</a>. Authors need to <a href="http://ojs.conferencepublication.com/index.phpaoc/user/register">register</a> with the journal prior to submitting or, if already registered, can simply <a href="http://ojs.conferencepublication.com/index.php/index/login">log in</a> and begin the five-step process.
            </div>`
        },
        "librarians": {
            "ushoq_text": "INFORMATION FOR LIBRARIANS",
            "title": "Information For Librarians",
            "body": `<div class="description">
            We encourage research librarians to list this journal among their library's electronic journal holdings. As well, it may be worth noting that this journal's open source publishing system is suitable for libraries to host for their faculty members to use with journals they are involved in editing (see <a href="http://pkp.sfu.ca/ojs">Open Journal Systems</a>).
        </div>`
        }
    })
    let {info} = useParams()
    
    return (
        <>
          <Header />
            <div className="sub_title">
                {
                    document.querySelector("title").textContent = `${data[info].title} | Archive of Conferences`
                }
            </div>
            <div className="pages_information container">
                <div className="pages_information_body">
                    <div className="ushoq">
                        <h6><Link className="link" to="/">HOME </Link> / <Link className="link" to={"/information/" + info}>{data[info].ushoq_text}</Link ></h6>
                    </div>
                    <h1>{data[info].title}</h1>
                    <div dangerouslySetInnerHTML={{ __html: data[info].body}}>

                    </div>
                </div>
                <div >
                    <Pages_menu />
                </div>
            </div>
            <Footer />
        </>
    )
}

export default Information;