import React from "react";
import "./Articles.scss"
import Header  from "../../Components/Header/Header"
import Pages_menu from "../../Components/Pages-menu/Pages_menu"
import Footer from "../../Components/Footer/Footer"
import {useEffect, useState, useRef} from "react"
import {useParams, Link} from "react-router-dom"
import {Dropdown} from 'react-bootstrap';
import img_kalonka from "../../asseste/images/88x31.png"
function Articles_more () {
    const {articles_id} = useParams()
    const [data_articles_more, set_data_articles_more] = useState({
        "articles_id": "2",
        "articles_title": "ANALYSIS OF THE STORY OF CROWS FROM WINTER",
        "articles_author": "Kholnazarova Dilnoza Rustam qizi",
        "articles_author_job": "Samarkand State University Kattakurgan branch students",
        "articles_long_text": "This story reveals the fate of a man who endured the trials of life. He too had the right to live well and to be fed by the love of his parents. But the trials of destiny deprived him of these things. But in time, as the moon was said to be fifteen bright and fifteen dark, a bright and bright future awaited him. 'In those days, instead of being a pillar, he was treated as an enemy. Nasibjon was also a man who understood what his relatives were like. He did not receive any love or help from any of his relatives during his life. This is also a test of destiny",
        "articles_date": "2022-01-30",
        "articles_archive": "1",
        "articles_keywords": "human, destiny, person, habitable",
        "articles_pdf": "http://uploadimg.systemfront.uz/images/myImage-System-front-1644053322022.pdf",
        "articles_page": "3-4",
        "archives_id": "1",
        "archives_date": "2022-02-01",
        "archives_img": "http://uploadimg.systemfront.uz/images/myImage-System-front-1644045381835.jpg",
        "archives_title": "EAHD"
    })
    useEffect(() => {
        fetch(`https://conferencepublication-backend.herokuapp.com/one_articles?articles_id=${articles_id}`, {
            method: "GET"
        }).then(res => res.json()).then(res => {
            set_data_articles_more(res)
        })
    }, []);
    return (
        <>
        <Header />
            <div className="sub_title">
                {
                    document.querySelector("title").textContent = `${data_articles_more.articles_title} | Archive of Conferences`
                }
            </div>
            <div className="pages_article_more container">
                <div className="pages_article_more_body">
                    <div className="ushoq">
                        <h6><Link className="link" to="/">HOME </Link> / <Link className="link" to="/archives" >Archives</Link > / <Link className="link" to={`/archives-more/${data_articles_more.archives_id}`} >{data_articles_more.archives_date.split("-")[0]}: {data_articles_more.archives_title}</Link > / ARTICLES</h6>
                        {console.log(data_articles_more) }
                    </div>
                    <h2>{data_articles_more.articles_title}</h2>
                    <div className="artcile-item">
                        <div className="right">
                            <img src={data_articles_more.archives_img} alt="" />
                            <Link className="link_pdf btn btn-primary"  to={`/open_pdf/${data_articles_more.articles_id}`}>PDF</Link>
                            <div className="articles-more-datas">
                                <p className="articles-more-datas_data"><strong>Published: </strong> {data_articles_more.articles_date}</p>
                                <p className="articles-more-ketwords"><strong>Keywords: </strong> <br/>
                                {data_articles_more.articles_keywords}</p>
                            </div>
                        </div>
                        <div className="left">
                            <strong  className="left-stong-text">{data_articles_more.articles_author}</strong>
                            <p>{data_articles_more.articles_author_job}</p>
                            <h3>Abstract</h3>
                            <p>{data_articles_more.articles_long_text}</p>
                            <div className="current_issue">
                                <div className="title-current_issue">
                                    HOW TO CITE
                                </div>
                                <p>Hayitova Moxichehra Najim Kizi. (2022). BASIC LAWS OF GROWTH IN THE DEVELOPMENT OF CHILDREN AND ADOLESCENTS. Archive of Conferences, 11-12. Retrieved from https://conferencepublication.com/index.php/aoc/article/view/1780</p>
                                
                                <Dropdown clasName="">
                                    <Dropdown.Toggle variant="dark" className="button-dropdown drop-down-more-articles" id="dropdown-basic">
							            More Citation Formats
									</Dropdown.Toggle>

                                    <Dropdown.Menu>
										<Dropdown.Item>
											<Link to="/" className="nav-link">
												ACM
											</Link>
										</Dropdown.Item>
										<Dropdown.Item>
											<Link to="/" className="nav-link">
												ACS
											</Link>
										</Dropdown.Item>
									    <Dropdown.Item>
											<Link to="/" className="nav-link">
												APA
											</Link>
										</Dropdown.Item>
										<Dropdown.Item>
											<Link to="/" className="nav-link">
												ABNT
											</Link>
										</Dropdown.Item>
									    <Dropdown.Item>
											<Link to="/" className="nav-link">
												Chicago
											</Link>
										</Dropdown.Item>
										<Dropdown.Item>
											<Link to="/" className="nav-link">
												Harvard
											</Link>
										</Dropdown.Item>
										<Dropdown.Item>
											<Link to="/" className="nav-link">
												IEEE
											</Link>
										</Dropdown.Item>
										<Dropdown.Item>
											<Link to="/" className="nav-link">
												MLA
											</Link>
										</Dropdown.Item>
										<Dropdown.Item>
											<Link to="/" className="nav-link" >
												Turabian
											</Link>
										</Dropdown.Item>
										<Dropdown.Item>
											<Link to="/" className="nav-link" >
												Vancouver
											</Link>
										</Dropdown.Item>
																
                                    </Dropdown.Menu>
                                </Dropdown>

                            </div>
                            <div className="current_issue ISSUE">
                                <div className="title-current_issue">
                                    ISSUE
                                </div>
                                <Link className="link" to={`/archives-more/${data_articles_more.archives_id}`} >{data_articles_more.archives_date.split("-")[0]}: {data_articles_more.archives_title}</Link >
                            </div>
                            <div className="current_issue ISSUE">
                                <div className="title-current_issue">
                                SECTION
                                </div>
                                <span className="text-section">Articles</span>
                            </div>
                            <div className="current_issue ISSUE">
                                <div className="galati_kalonkamenu">
                                    <img src={img_kalonka} alt="" />
                                    <p>This work is licensed under a <a rel="license" href="https://creativecommons.org/licenses/by-nc-nd/4.0/">Creative Commons Attribution-NonCommercial-NoDerivatives 4.0 International License</a>.</p>
                                </div>
                            </div>
                            <div className="addthis_toolbox addthis_default_style ">
                            <a class="addthis_button_print addthis_button_preferred_1 at300b at300b" title="Preferred_1" href="#" target="_blank"></a>
                            <a className="addthis_button_facebook"></a>
                            <a className="addthis_button_twitter"></a>
                            <a className="addthis_button_email"></a>
                            <a className="addthis_button_linkedin"></a>
                            <a className="addthis_button_compact"></a>
                            <a className="addthis_counter addthis_bubble_style" href="#" ><a className="addthis_button_expanded" target="_blank" title="More" href="#"></a><a className="atc_s addthis_button_compact">Share<span></span></a></a>
                            <div class="atclear"></div><div class="atclear"></div>
                            </div>
                        </div>
                    </div>
                    <div className="list-autors">
                        <h3>Most read articles by the same author(s)</h3>
                        <ul>
                            <li>
                            {data_articles_more.articles_author}<Link className="link-authors" to={`/articles-more/${data_articles_more.articles_id}`}>, {data_articles_more.articles_title}, </Link>
                        <Link to={`/archives-more/${data_articles_more.archives_id}`} className="link-authors">
							Archive of Conferences: {data_articles_more.archives_date.split("-")[0]}: {data_articles_more.archives_title}
						</Link>
                            </li>
                        </ul>
                    </div>
                </div>
                <div >
                    <Pages_menu />
                </div>
            </div>
            <Footer />
        </>
    )
}

export default Articles_more;