import React from "react";
import "./Home-admin.scss"
import Header_admin from "../../Components/Header-admin/Header-admin";
import {useParams} from "react-router-dom"
import {useState, useEffect, useRef} from "react"
function Home_admin () {
   
    const [data_archives, setdata_archives] = useState([])
    const [data_articles, setdata_articles] = useState([])

    useEffect(() => {
        fetch("https://conferencepublication-backend.herokuapp.com/archives", {
            method: "Get"
        }).then(res => res.json()).then(res => {
            setdata_archives(res)
        })
    }, []);

    useEffect(() => {
        fetch(`https://conferencepublication-backend.herokuapp.com/articles`, {
            method: "Get"
        }).then(res => res.json()).then(res => {
            setdata_articles(res)
            console.log(res)
        })
    }, []);

    // articles
    const input_articles_title = useRef()
    const input_articles_author = useRef()
    const input_articles_author_job = useRef()
    const input_articles_long_text = useRef()
    const input_articles_date = useRef()
    const select_articles_archive = useRef()
    const input_articles_keywords = useRef()
    const [pdf, setpdf] = useState()
    const input_articles_page = useRef()

        return (
            <>
                <Header_admin />
                <h1 className="text-center p-2">New Article</h1>
                <form className="container new-articles-list">
                    <input ref={input_articles_title} type="text" placeholder="articles title" />
                    <input ref={input_articles_author} type="text" placeholder="articles author" />
                    <input ref={input_articles_author_job} type="text" placeholder="articles author Job" />
                    <input ref={input_articles_long_text} type="text" placeholder="articles Long text" />
                    <input ref={input_articles_date} type="date" placeholder="articles data" />
                    <select ref={select_articles_archive} aria-label="Default select example" className="form-select"> 
                        <optgroup>
                            {data_archives.map((elem) => {
                                return (
                                    <option value={elem.archives_id} >{elem.archives_title}</option>
                                )
                            })}
                        </optgroup>
                    </select>
                    <input ref={input_articles_keywords} type="text" placeholder="articles Keywords" />
                    <input ref={input_articles_page} type="text" placeholder="articles pages" />
                    <input type="file" className="input-photo4 form-control"   id="inputGroupFile04" aria-describedby="inputGroupFileAddon04" aria-label="Upload" onChange={(e) => {
                         var formdata = new FormData();
                         formdata.append("myImage", document.querySelector(".input-photo4").files[0]);
                         
                         var requestOptions = {
                           method: 'POST',
                           body: formdata,
                           redirect: 'follow'
                         };
                         
                         fetch("http://uploadimg.systemfront.uz/upload-image", requestOptions)
                           .then(response => response.text())
                           .then(result => {
                               setpdf(result)
                           })
                           .catch(error => console.log('error', error));
                    }} />
                    <button className="btn" onClick={(e) => {
                        e.preventDefault()
                        var myHeaders = new Headers();
                        myHeaders.append("Content-Type", "application/json");

                        var raw = JSON.stringify({
                            "articles_title": input_articles_title.current.value,
                            "articles_author": input_articles_author.current.value,
                            "articles_author_job": input_articles_author_job.current.value,
                            "articles_long_text": input_articles_long_text.current.value,
                            "articles_date": input_articles_date.current.value,
                            "articles_archive": select_articles_archive.current.value,
                            "articles_keywords": input_articles_keywords.current.value,
                            "articles_pdf": pdf,
                            "articles_page": input_articles_page.current.value,
                            "token": window.localStorage.getItem("token")
                        });

                        var requestOptions = {
                        method: 'POST',
                        headers: myHeaders,
                        body: raw,
                        redirect: 'follow'
                        };

                        fetch("https://conferencepublication-backend.herokuapp.com/new_article", requestOptions)
                        .then(response => response.text())
                        .then(result => {
                            console.log(result)
                            if(result == "ok") {
                                alert("succses")
                                window.location.reload()
                            }else{
                                alert("error: qandaydur hatolik bor iltmos keyinroq urnib koring")
                            }
                        })
                        .catch(error =>  alert("error: qandaydur hatolik bor iltmos keyinroq urnib koring"));
                    }}>Send</button>
                </form>
                <div className="container">
                    <h1 className="text-center p-2 mt-5">Article</h1>
                    <select aria-label="Default select example" className="form-select w-25 select-archive" onChange={(e) => {
                        fetch(`https://conferencepublication-backend.herokuapp.com/articles_for_archive?articles_archive=${e.target.value}`, {
                            method: "Get"
                        }).then(res => res.json()).then(res => {
                            setdata_articles(res)
                        })
                    }}> 
                        <optgroup>
                            {data_archives.map((elem) => {
                                return (
                                    <option value={elem.archives_id} >{elem.archives_title}</option>
                                )
                            })}
                        </optgroup>
                    </select>
                    <ul className="articles-list container">
                        {data_articles.map((elem) => {
                            return (
                                <div key={elem.articles_archive} class="card" >
                                    <iframe src={elem.articles_pdf} width="250px" class="card-img-top"/>
                                    <div class="card-body">
                                    <h5 class="card-title">{elem.articles_title}</h5>
                                    <h6 class="card-text">{elem.articles_author}</h6>
                                    <p class="card-text">{elem.articles_author_job}</p>
                                    
                                    <p class="card-text">{elem.articles_long_text}</p>
                                    <button className="btn" data-uuid={elem.articles_id} onClick={(e) => {
                                       let sorov = prompt("rostan ham bu malumotni ochirmoqchimisiz (Y/n)")
                                       if(sorov === "Y"){
                                        var myHeaders = new Headers();
                                        myHeaders.append("Content-Type", "application/json");
                                        var raw = JSON.stringify({
                                            "articles_id": e.target.dataset.uuid,
                                            "token": window.localStorage.getItem("token")
                                        })
                                        var requestOptions = {
                                            method: 'POST',
                                            headers: myHeaders,
                                            body: raw,
                                            redirect: 'follow'
                                            };
                                        fetch("https://conferencepublication-backend.herokuapp.com/delete_article", requestOptions).then(res => res.text()).then(res => {
                                            if(res == "ok") {
                                                window.location.reload()
                                            }
                                        })
                                       }else {

                                       }
                                    }}>Delete</button>
                                    </div>
                                </div>
                            )
                        })}
                    </ul>
                </div>
            </>
        )
}

export default Home_admin;