import React from "react";
import 'bootstrap/dist/css/bootstrap.min.css';
import "./Header-admin.scss";
import { NavLink } from "react-router-dom";
import {Dropdown} from 'react-bootstrap';

function Header_admin () {
    return (
        <header >
            <nav>
                <NavLink to="/login" className="nav-link login-header">Log out</NavLink>
                <div className="header-menu">
                <div className="header-left">
                    <h6><NavLink to="/" className="nav-link">Archive of Conferences</NavLink></h6>
                    <ul>
                        <li><NavLink to="/admin-panel/Articles" className="nav-link" >Articles</NavLink></li>
                        <li><NavLink to="/admin-panel/ARCHIVES" className="nav-link" >ARCHIVES</NavLink></li>
                        <li><NavLink to="/admin-panel/Papers" className="nav-link" >Papers</NavLink></li>
                 
                    </ul>
                </div>
                <div className="header-right">
                    <input type="text" />
                    <button className="btn">Search</button>
                </div>
                </div>
            </nav>
        </header>
    )
}

export default Header_admin;
