import React from "react";
import "./login.scss"
import Header  from "../../Components/Header/Header"
import Pages_menu from "../../Components/Pages-menu/Pages_menu"
import Footer from "../../Components/Footer/Footer"
import {Link} from "react-router-dom"
function Resstart_password () {
    return (
        <>
        
        <Header />
             <div className="sub_title">
                {
                    document.querySelector("title").textContent = `Reset Password | Archive of Conferences`
                }
            </div>
            <div className="pages_login container">
                <div className="pages_login_body">
                    <div className="ushoq">
                        <h6><Link className="link" to="/">HOME </Link> / <Link className="link" to="/login">LOGIN</Link ></h6>
                    </div>
                    <div className="alert alert-primary "> 
                    Enter your account email address below and an email will be sent with instructions on how to reset your password.
                    </div>
                    <div className="login-inputs">
                        <label>Registered user's email</label>
                        <input type="text" placeholder="Registered user's email" />
                    </div>
                    <button className="login-send-button">
                        Reset Password
                    </button>
                </div>
                <div >
                    <Pages_menu />
                </div>
            </div>
            <Footer />
        </>
    )
}
export default Resstart_password;