import React from "react";
import "./login.scss"
import Header  from "../../Components/Header/Header"
import Pages_menu from "../../Components/Pages-menu/Pages_menu"
import Footer from "../../Components/Footer/Footer"
import {Link} from "react-router-dom"
import {useState, useEffect, useRef} from "react"
function Login () {
    const input_username =useRef()
    const input_userpassword = useRef()

    return (
        <>
            <Header />
            <div className="sub_title">
                {
                    document.querySelector("title").textContent = `Login | Archive of Conferences`
                }
            </div>
            <div className="pages_login container">
                <div className="pages_login_body">
                    <div className="ushoq">
                        <h6><Link className="link" to="/">HOME </Link> / <Link className="link" to="/login">LOGIN</Link ></h6>
                    </div>
                    <div className="login-inputs">
                        <label>Username</label>
                        <input required ref={input_username} type="text" placeholder="Username" />
                    </div>
                    <div className="login-inputs">
                        <label>Password</label>
                        <input required ref={input_userpassword} type="password" placeholder="Password" />
                    </div>
                    <Link to="/resend_password" className="login-forger-text">Forgot your password?</Link>
                    <div>
                        <input type="checkbox" id="chesk" />
                        <label for="chesk">Keep me logged in</label>
                    </div>
                    <button className="login-send-button" onClick={(e) => {
                        e.preventDefault()
                        var myHeaders = new Headers();
                        myHeaders.append("Content-Type", "application/json");
                        
                        var raw = JSON.stringify({
                          "user_name": input_username.current.value,
                          "user_password": input_userpassword.current.value
                        });
                        
                        var requestOptions = {
                          method: 'POST',
                          headers: myHeaders,
                          body: raw,
                          redirect: 'follow'
                        };
                        
                        fetch("https://conferencepublication-backend.herokuapp.com/login_user", requestOptions)
                          .then(response => response.text())
                          .then(result => {
                              if(result === "user not found"){
                                  alert("user not found")
                              }else {
                                  let response = JSON.parse(result)
                                  window.localStorage.setItem("token", response[0])
                                  if(response[1] === "admin"){
                                      window.location = "/admin-panel/Articles"
                                  }else {
                                      window.location = "/"
                                  }
                              }
                          })
                          .catch(error => console.log('error', error));
                    }} >Login</button>
                </div>
                <div >
                    <Pages_menu />
                </div>
            </div>
            <Footer />
        </>
    )
}

export default Login;