import React from "react"
import "./Home.scss"
import Header  from "../../Components/Header/Header"
import Pages_menu from "../../Components/Pages-menu/Pages_menu"
import Footer from "../../Components/Footer/Footer"
import {useEffect, useState, useRef} from "react"
import {Link} from "react-router-dom"
function Home () {
    const [data_articles, setdata_articles] = useState([])
    const [data_archive, setdata_archive] = useState({
        "archives_id": "3",
        "archives_date": "2022-01-13",
        "archives_img": "http://uploadimg.systemfront.uz/images/myImage-System-front-1644044217734.jpg",
        "archives_title": "HSC"
    })
    useEffect(() => {
        fetch("https://conferencepublication-backend.herokuapp.com/archives", {
            method: "GET"
        }).then(res => res.json()).then(res => {
            console.log(res)
        })
    }, []);
    useEffect(() => {
        fetch(`https://conferencepublication-backend.herokuapp.com/one_archive?Archives_id=1`, {
            method: "GET"
        }).then(res => res.json()).then(res => {
            setdata_archive(res)
        })
    }, []);
    useEffect(() => {
        fetch(`https://conferencepublication-backend.herokuapp.com/articles_for_archive?articles_archive=1`, {
            method: "GET"
        }).then(res => res.json()).then(res => {
            res.sort((a, b) => a.articles_page - b.articles_page)
            setdata_articles(res)
        })
    }, []);
    return (
        <>
            <Header />
            <div className="sub_title">
                {
                    document.querySelector("title").textContent = "Archive of Conferences"
                }
            </div>
            <div className="pages_home container">
                <div className="pages_home_body">
                    <p>Archive of Conferences (AoC) is a platform created to publish the research papers of the conferences organized worldwide. We are providing the publication support to the conference organizers in order to make the research available online. </p>
                    <p>AIC platform works for the collaboration between the Academic and Scientific Institutions to publish the conference proceedings. We are also providing the services for indexing of the papers in reputed databases.</p>
                    <p>We invite the conference organizers all over the world to publish the special issues of the State, National and International Conferences, Symposium, Events. </p>

                    <h2>Current Issue</h2>
                    <div className="pages_archives_more_body home_archive">
                    <div className="archive-more">
                        <Link to={`/archives-more/${data_archive.archives_id}`} ><img src={data_archive.archives_img} alt="" /></Link>
                        <p><span>Published:</span>{data_archive.archives_date}</p>
                    </div>
                    <h3>Articles</h3>
                    <div className="archive-article">
                         {data_articles.map((elem) => {
                             return (
                                 <div className="archive-articles_item">
                                     <Link className="archive-articles_item_link" to={`/articles-more/${elem.articles_id}`}>{elem.articles_title}</Link>
                                     <h6>{elem.articles_author}</h6>
                                     <p>{elem.articles_page}</p>
                                     <Link to={`/open_pdf/${elem.articles_id}`} className="link_pdf btn btn-primary" >PDF</Link>
                                 </div>
                             )
                         })}
                    </div>
                </div>
                </div>
                <div >
                    <Pages_menu />
                </div>
            </div>
            <Footer />
        </>
    )
}


export default Home;