import React from "react";
import "./Home-admin.scss"
import Header_admin from "../../Components/Header-admin/Header-admin";
import {useParams} from "react-router-dom"
import {useState, useEffect, useRef} from "react"

function Archive_page_admin () {
    const [uuid, setuuid] = useState(0)
    const [modal_edit, setmodal_edit] = useState("hide")
    const input_title_edit = useRef()
    const input_short_edit = useRef()
    const [blog_photo_edit, setblog_photo_edit] = useState()
    const [data_archives, setdata_archives] = useState([])

    const input_archive_title = useRef()
    const input_archive_date = useRef()
    const [photo_archive, setphoto_archive] = useState("")
       useEffect(() => {
        fetch("https://conferencepublication-backend.herokuapp.com/archives", {
            method: "Get"
        }).then(res => res.json()).then(res => {
            setdata_archives(res)
        })
    }, []);

    return (
        <>
            <Header_admin />
            
            <h1 className="text-center p-2">New Archive</h1>
            <form className="container new-arcives-list">
                <input ref={input_archive_title} type="text" placeholder="Archive title" />
                <input ref={input_archive_date} type="date" placeholder="Archive date" />
                <input type="file" className="input-photo2 form-control"   id="inputGroupFile04" aria-describedby="inputGroupFileAddon04" aria-label="Upload" onChange={(e) => {
                     var formdata = new FormData();
                     formdata.append("myImage", document.querySelector(".input-photo2").files[0]);
                     
                     var requestOptions = {
                       method: 'POST',
                       body: formdata,
                       redirect: 'follow'
                     };
                     
                     fetch("http://uploadimg.systemfront.uz/upload-image", requestOptions)
                       .then(response => response.text())
                       .then(result => {
                           setphoto_archive(result)
                       })
                       .catch(error => console.log('error', error));
                }} />
                <button className="btn" onClick={(e) => {
                    e.preventDefault()
                    var myHeaders = new Headers();
                    myHeaders.append("Content-Type", "application/json");

                    var raw = JSON.stringify({
                        "Archives_date": input_archive_date.current.value,
                        "Archives_img": photo_archive,
                        "Archives_title": input_archive_title.current.value,
                        "token": window.localStorage.getItem("token")
                    });

                    var requestOptions = {
                    method: 'POST',
                    headers: myHeaders,
                    body: raw,
                    redirect: 'follow'
                    };

                    fetch("https://conferencepublication-backend.herokuapp.com/new_archive", requestOptions)
                    .then(response => response.text())
                    .then(result => {
                        console.log(result)
                        if(result == "ok") {
                            alert("succses")
                            window.location.reload()
                        }else{
                            alert("error: qandaydur hatolik bor iltmos keyinroq urnib koring")
                        }
                    })
                    .catch(error => console.log('error', error));
                }}>Send</button>
            </form>
            <div>
                <h1 className="text-center p-2 mt-5">Archives</h1>

                <ul className="archives-list container">
                    {data_archives.map((elem) => {
                        return (
                            <div key={elem.archives_id} class="card" >
                                <img src={elem.archives_img} class="card-img-top"/>
                                <div class="card-body">
                                <h5 class="card-title">{elem.archives_title}</h5>
                                <p class="card-text">{elem.archives_date}</p>
                                <button className="btn" data-uuid={elem.archives_id} onClick={(e) => {
                                    setmodal_edit("show")
                                    setuuid(e.target.dataset.uuid)
                                }}>Edit</button>
                                </div>
                            </div>
                        )
                    })}
                </ul>
            </div>
            <div className={`edit-modal ${modal_edit}`}>
              <div className="edit-modal-content">
              <input ref={input_title_edit} type="text" maxLength="1000" required placeholder="Archive new title" />
            <input ref={input_short_edit} type="date" maxLength="2500" required placeholder="" />
           
            <input className="input-photo3" type="file" onChange={() => {
                var formdata = new FormData();
                formdata.append("myImage", document.querySelector(".input-photo3").files[0]);
                
                var requestOptions = {
                  method: 'POST',
                  body: formdata,
                  redirect: 'follow'
                };
                
                fetch("http://uploadimg.systemfront.uz/upload-image", requestOptions)
                  .then(response => response.text())
                  .then(result => {
                      console.log(result)
                      setblog_photo_edit(result)
                  })
                  .catch(error => console.log('error', error));
            }} />
            <button className="btn" onClick={(e) => {
                 e.preventDefault()
                 var myHeaders = new Headers();
                 myHeaders.append("Content-Type", "application/json");

                 var raw = JSON.stringify({
                    "Archives_title": input_title_edit.current.value,
                    "Archives_date": input_short_edit.current.value,
                    "Archives_img": blog_photo_edit,
                    "Archives_id": uuid,
                    "token": window.localStorage.getItem("token")
                 });

                 var requestOptions = {
                 method: 'POST',
                 headers: myHeaders,
                 body: raw,
                 redirect: 'follow'
                 };

                 fetch("https://conferencepublication-backend.herokuapp.com/update_archive", requestOptions)
                 .then(response => response.text())
                 .then(result => {
                     console.log(result)
                     if(result == "ok") {
                         window.location.reload()
                     }
                 })
                 .catch(error => console.log('error', error));
            }}>
                SEND
            </button>
              </div>
            <button className="close-edit-button" onClick={() => {
                setmodal_edit("hide")
            }}>&times;</button>
        </div>
        </>
    )
}

export default Archive_page_admin;