import React from "react";
import "./search.scss"
import Header  from "../../Components/Header/Header"
import Pages_menu from "../../Components/Pages-menu/Pages_menu"
import Footer from "../../Components/Footer/Footer"
import {useParams, Link} from "react-router-dom"
import {useState, useRef, useEffect} from "react"
function Search () {
    const search_input = useRef()
    const {article} = useParams()
    const [value_found, setvalue_found]= useState("hide")
    const [search_data_articl, set_search_data_article] = useState(["yuklash"])
    useEffect(() => {
        fetch(`https://conferencepublication-backend.herokuapp.com/search_article?key=%${article}%`, {
            method:"GET"
        }).then(res => res.json()).then(res => {
            if(res == "false"){
                set_search_data_article("false")
            }else if(res.length) {
                set_search_data_article(res)
            }else{
                set_search_data_article([res])
            }
        })
    }, []);
    return (
        <>
         <Header />
            <div className="sub_title">
                {
                    document.querySelector("title").textContent = "Search | Archive of Conferences"
                }
            </div>
            <div className="pages_search container">
                <div className="pages_search_body">
                    <h2>Search</h2>
                    <div class="input-group mb-3">
                        <input ref={search_input} type="text" className="form-control" defaultValue={article} aria-label="Recipient's username" aria-describedby="button-addon2" />
                        <button className="btn btn-outline-secondary" onClick={(e) => {
                            let orign= window.location.origin
                            window.location = orign +  "/search/" + search_input.current.value
                        }} type="button" id="button-addon2">SEARCH</button>
                    </div>
                    <h4>Advanced filters</h4>
                    <div className="form_select_menu">
                        <div className="left">
                            <strong for="select_after">Published After</strong>

                            <div>
                            <select id="select_after">
                                <option value="" selected key=""></option>
                                <option value="2020"  key="">2020</option>
                                <option value="2021"  key="">2021</option>
                                <option value="2022"  key="">2022</option>
                            </select>
                            <select name="dateFromMonth">
                                <option value=""></option>
                                <option value="01">January</option>
                                <option value="02">February</option>
                                <option value="03">March</option>
                                <option value="04">April</option>
                                <option value="05">May</option>
                                <option value="06">June</option>
                                <option value="07">July</option>
                                <option value="08">August</option>
                                <option value="09">September</option>
                                <option value="10">October</option>
                                <option value="11">November</option>
                                <option value="12">December</option>
                            </select>
                            <select name="dateFromDay">
                                <option value=""></option>
                                <option value="1">01</option>
                                <option value="2">02</option>
                                <option value="3">03</option>
                                <option value="4">04</option>
                                <option value="5">05</option>
                                <option value="6">06</option>
                                <option value="7">07</option>
                                <option value="8">08</option>
                                <option value="9">09</option>
                                <option value="10">10</option>
                                <option value="11">11</option>
                                <option value="12">12</option>
                                <option value="13">13</option>
                                <option value="14">14</option>
                                <option value="15">15</option>
                                <option value="16">16</option>
                                <option value="17">17</option>
                                <option value="18">18</option>
                                <option value="19">19</option>
                                <option value="20">20</option>
                                <option value="21">21</option>
                                <option value="22">22</option>
                                <option value="23">23</option>
                                <option value="24">24</option>
                                <option value="25">25</option>
                                <option value="26">26</option>
                                <option value="27">27</option>
                                <option value="28">28</option>
                                <option value="29">29</option>
                                <option value="30">30</option>
                                <option value="31">31</option>
                            </select>
                            </div>
                            <strong for="select_after">Published Before</strong>

                            <div>
                            <select id="select_after">
                                <option value="" selected key=""></option>
                                <option value="2020"  key="">2020</option>
                                <option value="2021"  key="">2021</option>
                                <option value="2022"  key="">2022</option>
                            </select>
                            <select name="dateFromMonth">
                                <option value=""></option>
                                <option value="01">January</option>
                                <option value="02">February</option>
                                <option value="03">March</option>
                                <option value="04">April</option>
                                <option value="05">May</option>
                                <option value="06">June</option>
                                <option value="07">July</option>
                                <option value="08">August</option>
                                <option value="09">September</option>
                                <option value="10">October</option>
                                <option value="11">November</option>
                                <option value="12">December</option>
                            </select>
                            <select name="dateFromDay">
                                <option value=""></option>
                                <option value="1">01</option>
                                <option value="2">02</option>
                                <option value="3">03</option>
                                <option value="4">04</option>
                                <option value="5">05</option>
                                <option value="6">06</option>
                                <option value="7">07</option>
                                <option value="8">08</option>
                                <option value="9">09</option>
                                <option value="10">10</option>
                                <option value="11">11</option>
                                <option value="12">12</option>
                                <option value="13">13</option>
                                <option value="14">14</option>
                                <option value="15">15</option>
                                <option value="16">16</option>
                                <option value="17">17</option>
                                <option value="18">18</option>
                                <option value="19">19</option>
                                <option value="20">20</option>
                                <option value="21">21</option>
                                <option value="22">22</option>
                                <option value="23">23</option>
                                <option value="24">24</option>
                                <option value="25">25</option>
                                <option value="26">26</option>
                                <option value="27">27</option>
                                <option value="28">28</option>
                                <option value="29">29</option>
                                <option value="30">30</option>
                                <option value="31">31</option>
                            </select>
                            </div>
                        </div>
                        <div className="right">
                            <strong>By Author</strong>
                            <input type="text" name="By_Author" />
                        </div>
                    </div>
                    <div>
                        <h2 className="mt-3">Search Results</h2>
                        {console.log(String(search_data_articl))}
                        {String(search_data_articl) === "false" ?  
                        <div className={`alert alert-primary show`}> 
                            Login or Register to make a submission.
                        </div> : 
                            search_data_articl.map((elem) => {
                                return (
                                    <div key={elem.articles_id} className="archive-articles_item">
                                     <Link className="archive-articles_item_link" to={`/articles-more/${elem.articles_id}`}>{elem.articles_title}</Link>
                                     <h6>{elem.articles_author}</h6>
                                     <p>{elem.articles_page}</p>
                                 </div>
                                )
                            })
                        }
                    </div>
                </div>
                <div >
                    <Pages_menu />
                </div>
            </div>
            <Footer />
        </>
    )
}

export default Search;