function Header () {
    const search_input = React.useRef()
    return (
        <header>
            {window.localStorage.getItem("token") ? <NavLink to="/admin-panel/Articles" className="nav-link login-header">Admin_panel</NavLink> : <NavLink to="/login" className="nav-link login-header">LOGIN</NavLink>}
            <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
            <div className="container-fluid">
                <NavLink to="/" className="navbar-brand" href="#">Archive of Conferences</NavLink>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                    <li className="nav-item">
                    <NavLink  to="/archives-more/1" className="nav-link active" aria-current="page" href="#">Current</NavLink>
                    </li>
                    <li className="nav-item">
                    <NavLink to="/archives" className="nav-link" href="#">Archives</NavLink>
                    </li>
                    <li className="nav-item">
                    <NavLink to="/papers" className="nav-link" href="#">Call for Papers</NavLink>
                    </li>
                    <li class="nav-item dropdown">
                    <Dropdown>
                            <Dropdown.Toggle variant="dark" className="button-dropdown" id="dropdown-basic">About</Dropdown.Toggle>
                            <Dropdown.Menu>
                                <Dropdown.Item><NavLink to="/submissions" className="nav-link text-dark">Submissions</NavLink></Dropdown.Item>
                                <Dropdown.Item><NavLink to="/contact" className="nav-link text-dark">Contact</NavLink></Dropdown.Item>
                            </Dropdown.Menu>
                        </Dropdown>
                    </li>
                </ul>
                <form className="d-flex header-right">
                    <input className="form-control me-2 " type="search"  aria-label="Search" ref={search_input} />
                    <button className="btn text-light button_search"  onClick={(e) => {
                        e.preventDefault()
                        let orign= window.location.origin
                        window.location = orign +  "/search/" + search_input.current.value
                    }} >Search</button>
                </form>
                </div>
            </div>
            </nav>
        </header>
    )
}

export default Header;
